@import url(https://fonts.googleapis.com/css2?family=Oswald&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Cabin+Sketch:wght@400;700&display=swap);
@import url(//db.onlinewebfonts.com/c/0c724f6aa457310440cf8949c615cbd7?family=Star+Jedi);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  font-family: "Oswald", sans-serif;
  letter-spacing: 0.1em;
}

body {
  background-color: #210d28;
}

button:focus {
  outline: none;
}

.header__container {
  background: url(/react-starwars/static/media/HeaderSWAPI.3f632548.svg) no-repeat top right;
  background-size: cover;
  background-color: #210d28;
  height: 300px;
  padding: 2em;
  display: grid;
  grid-template-areas:
    "everyone"
    "favorites"
    "addFav"
    "search";
  grid-gap: 0.5em;
  gap: 0.5em;
  place-items: center;
  justify-content: center;
}

.header__container-title {
  font-family: "Star Jedi", arial;
  font-size: 5em;
  margin: 0 1rem;
  padding: 1rem 2rem;
  text-align: center;
  line-height: 1;
  display: none;
  grid-area: titletext;
}

.navbar__button {
  font-family: "Cabin Sketch", cursive;
  font-weight: 700;
  color: yellow;
  font-size: 1.5em;
  text-transform: uppercase;
  text-decoration: none;
  border: 1px solid rgb(33, 13, 40);
  border-radius: 0.4em;
  position: relative;
  overflow: hidden;
  background: rgba(33, 13, 40, 0.7);
  width: 300px;
  height: 55px;
}

.navbar__button:hover {
  box-shadow: 1px 1px 25px 10px rgba(33, 13, 40, 0.4);
}

.selected {
  background: #ddca92;
  color: rgb(33, 13, 40);
}

.navbar__link-everyone {
  grid-area: everyone;
}

.navbar__link-favorites {
  grid-area: favorites;
}

.navbar__link-addfavorite {
  grid-area: addFav;
}

.searchfield__input {
  padding: 0.4em;
  font-size: 1em;
  text-transform: uppercase;
  text-decoration: none;
  border: 1px solid rgb(33, 13, 40);
  border-radius: 0.4em;
  position: relative;
  overflow: hidden;
  background: rgba(33, 13, 40, 0.7);
  width: 300px;
  height: 55px;
  grid-area: search;
}

@media (min-width: 580px) {
  .header__container {
    grid-template-areas:
      "titletext titletext"
      "everyone favorites"
      "search addFav";
  }

  .header__container-title {
    display: block;
    font-size: 4em;
  }

  .navbar__button {
    width: 250px;
  }
  
  .searchfield__input {
      width: 250px;
  }
}

@media (min-width: 900px) {
}

@media (min-width: 1200px) {
  .header__container-title {
    margin: 0 auto;
    padding: 1rem 2rem;
  }
}

.card__container {
    color: yellow;
    margin: 1em;
    padding: 1.5em;
    border: 1px solid yellow;
    display: grid;
    grid-template: 1.5fr 1fr 1fr 1.5fr / 1fr;
    width: 400px;
    text-transform: uppercase;
    box-shadow: 1px 1px 3px yellow;
}

.card__container>p {
    padding: 0 1em;
}

.card__name {
    justify-self: center;
    font-size: 1.5em;
    text-transform: uppercase;
}

.btn__fav {
    background: none;
    border: none;
    color: yellow;
    font-size: 2em;
    max-width: 30px;
    justify-self: center;
}

.btn__fav:hover {
    text-shadow: 1px 1px 2px black, 0 0 15px yellow, 0 0 5px rgba(255, 255, 0, 0.4);
}
.infocard__container {
  display: flex;
  flex-flow: row wrap;
  justify-content: center;
  align-items: flex-start;
}

.favlist__nofav {
    text-align: center;
    padding-top: 2em;
    font-size: 1.5em;
    color: yellow;
}
form {
  margin-top: 2em;
  display: grid;
  grid-gap: 0.4em;
  gap: 0.4em;
  grid-template-areas:
    "labelname"
    "inputname"
    "labelgender"
    "inputgender"
    "labelbirthyear"
    "inputbirthyear"
    "buttonsubmit";
  place-items: center;
}

.addfavorite__input {
  margin-bottom: 1.6em;
  padding: 0.7em;
  font-size: 1em;
  text-transform: uppercase;
  text-decoration: none;
  border: 1px solid rgb(33, 13, 40);
  border-radius: 0.4em;
  position: relative;
  overflow: hidden;
  background: rgba(175, 74, 89, 0.7);
  min-width: 300px;
}

label {
  color: yellow;
  font-size: 1.5em;
}

.labelname {
  grid-area: labelname;
}

.labelgender {
  grid-area: labelgender;
}
.labelbirthyear {
  grid-area: labelbirthyear;
}

.inputname {
  grid-area: inputname;
}

.inputgender {
  grid-area: inputgender;
}

.inputbirthyear {
  grid-area: inputbirthyear;
}

.submitbutton {
  grid-area: buttonsubmit;
  font-family: "Cabin Sketch", cursive;
  font-weight: 700;
  color: yellow;
  padding: 0.7em;
  font-size: 1.5em;
  text-transform: uppercase;
  text-decoration: none;
  border: 1px solid rgb(175, 74, 89);
  border-radius: 0.4em;
  position: relative;
  overflow: hidden;
  background: rgba(175, 74, 89, 0.7);
}

.submitbutton:hover {
  box-shadow: 1px 1px 15px 5px rgba(175, 74, 89, 0.4);
}

